#list{
    height: auto;
    width: 77%;
    background-color:white;
    margin-left: 11.5%;
    margin-top: 30px;
    padding-bottom: 30px;
    border: 2px solid rgb(155, 248, 143);
}
#list>h1{
    color: white;
    width: 100%;
    height: 50px;
    /* background-color: rgb(62, 224, 189); */
    background-color: cadetblue;
    text-align: center;
    font-size: 40px;
}
#list>h3{
   padding: 3px;
    color: black;
    margin-top: 30px;
    background-color: rgb(155, 248, 143);
    /* padding-left: 34%; */
    text-align: center;
    margin-bottom: 20px;
    font-size: 22px;
}
#list>li{
    padding-left: 20px;
    margin-top: 20px;

}
.a{
    color:  blue;
    font-size: 18px;
    font-weight: bolder;
}
.a:hover{
    color: black;
}
@media only screen and (max-width: 723px) {
    #list{
        margin-left: 0;
        width: 100%;
    }
    #list>h1{
        height: 40px;
        font-size: 28px;
    }
    #list>h3{
        font-size: 15px;
    }
    .a{
        font-size: 10px;
    }
    #list>li{
        padding-left: 15px;
        margin-top: 10px;
    }
}